(function() {
  var app;

  app = angular.module('evothingsapp');

  app.controller("MedIndexController", [
    "$scope", "$rootScope", "$state", "MedService", "$cordovaBLE", "$cordovaLocalNotification", "onBottleFound", function($scope, $rootScope, $state, MedService, $cordovaBLE, $cordovaLocalNotification, onBottleFound) {
      MedService.listMeds().then(function(meds) {
        var i, len, med;
        for (i = 0, len = meds.length; i < len; i++) {
          med = meds[i];
          med.edit = false;
        }
        return $scope.meds = meds;
      });
      $scope.newMed = {
        name: "",
        description: "",
        product_id: "",
        last_opened: "",
        openinterval: "",
        open_alert: "",
        reminder_on: ""
      };
      $scope.device = {};
      $scope.interrupted = false;
      $scope.scheduleSingleNotification = function(title, text) {
        return $cordovaLocalNotification.schedule({
          id: 1,
          title: title,
          text: text,
          data: {}
        }).then(function() {});
      };
      $scope.scanBT = function() {
        $scope.lastBottleDetected = 0;
        $scope.currBottleDetected = 0;
        $scope.lastName = "";
        $scope.deviceFound = false;
        return $scope.loopBTScan();
      };
      $scope.loopBTScan = function() {
        console.log("Loop begins");
        return document.addEventListener("deviceready", function() {
          return ble.startScan(['0000' + '0010' + '-0000-1000-8000-00805F9B34FB'], function(device) {
            onBottleFound.broadcast(new Date(), device.name);
            return ble.stopScan(function() {
              return $scope.loopBTScan();
            });
          });
        });
      };
      $scope.$on("bottlefound", function(e, nextDate, nextName) {
        var timeDifference;
        console.log("MEDFRIEND next date " + nextDate);
        console.log("MEDFRIEND next name " + nextName);
        if ($scope.lastBottleDetected === 0) {
          $scope.lastBottleDetected = nextDate;
        } else {
          $scope.lastBottleDetected = $scope.currBottleDetected;
        }
        if ($scope.currBottleDetected === 0) {
          $scope.currBottleDetected = $scope.lastBottleDetected;
        } else {
          $scope.currBottleDetected = nextDate;
        }
        console.log("MEDFRIEND last bottle date: " + $scope.lastBottleDetected);
        console.log("MEDFRIEND curr bottle date: " + $scope.currBottleDetected);
        if ($scope.lastBottleDetected !== 0) {
          timeDifference = $scope.currBottleDetected.getTime() - $scope.lastBottleDetected.getTime();
          console.log("MEDFRIEND time diff: " + timeDifference);
        }
        if ($scope.lastName !== nextName || timeDifference > 5500) {
          $scope.scheduleSingleNotification(nextName, "last signal: " + timeDifference);
          $scope.lastName = nextName;
          return console.log("MEDFRIEND name to be saved: " + $scope.lastName);
        }
      });
      $scope.flipEdit = function(index) {
        if ($scope.meds[index].edit) {
          return $scope.meds[index].edit = false;
        } else {
          return $scope.meds[index].edit = true;
        }
      };
      $scope.update = function(index) {
        return MedService.updateMed($scope.meds[index]).then(function() {
          return $state.go("root.meds.index", {}, {
            reload: true
          });
        });
      };
      $scope.save = function() {
        return MedService.createMed($scope.newMed).then(function() {
          return $state.go("root.meds.index", {}, {
            reload: true
          });
        });
      };
      return $scope["delete"] = function(index) {
        return $scope.meds[index].remove().then(function() {
          return $state.go("root.meds.index", {}, {
            reload: true
          });
        });
      };
    }
  ]);

}).call(this);



(function() {
  angular.module("evothingsapp").factory("MedService", [
    "Restangular", function(Restangular) {
      var MedRestangular, model;
      MedRestangular = Restangular.withConfig(function(RestangularConfigurer) {
        return RestangularConfigurer.addRequestInterceptor(function(elem, operation, what, url) {
          if (operation === "put" || operation === "post") {
            return {
              medication: elem
            };
          } else {
            return elem;
          }
        });
      });
      model = "medications";
      return {
        listMeds: function() {
          return MedRestangular.all(model).getList();
        },
        getMed: function(medId) {
          return MedRestangular.one(model, medId).get();
        },
        createMed: function(med) {
          return MedRestangular.service(model).post(med);
        },
        updateMed: function(med) {
          return med.put();
        }
      };
    }
  ]);

}).call(this);



(function() {
  angular.module('evothingsapp').directive('acmeMalarkey', function() {
    var MalarkeyController, directive, linkFunc;
    MalarkeyController = function($log, githubContributor) {
      'ngInject';
      var activate, getContributors, vm;
      vm = this;
      activate = function() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      };
      getContributors = function() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;
          return vm.contributors;
        });
      };
      vm.contributors = [];
      activate();
    };
    linkFunc = function(scope, el, attr, vm) {
      var typist, watcher;
      watcher = void 0;
      typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });
      el.addClass('acme-malarkey');
      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause()["delete"]();
      });
      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause()["delete"]();
        });
      });
      scope.$on('$destroy', function() {
        watcher();
      });
    };
    return directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };
  });

}).call(this);



(function() {
  angular.module('evothingsapp').factory('githubContributor', function($log, $http) {
    'ngInject';
    var apiHost, getContributors, service;
    apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';
    getContributors = function(limit) {
      var getContributorsComplete, getContributorsFailed;
      if (limit == null) {
        limit = 30;
      }
      getContributorsComplete = function(response) {
        return response.data;
      };
      getContributorsFailed = function(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      };
      return $http.get(apiHost + '/contributors?per_page=' + limit).then(getContributorsComplete)["catch"](getContributorsFailed);
    };
    return service = {
      apiHost: apiHost,
      getContributors: getContributors
    };
  });

}).call(this);



(function() {
  angular.module('evothingsapp').controller('MainController', function($timeout, webDevTec, toastr) {
    'ngInject';
    var activate, getWebDevTec, showToastr, vm;
    vm = this;
    activate = function() {
      getWebDevTec();
      $timeout((function() {
        vm.classAnimation = 'rubberBand';
      }), 4000);
    };
    showToastr = function() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    };
    getWebDevTec = function() {
      vm.awesomeThings = webDevTec.getTec();
      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    };
    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1445705343990;
    vm.showToastr = showToastr;
    activate();
  });

}).call(this);



